//other import
import React from 'react';
//styles
import styles from './Toolbar.module.scss';
//my components
import Menu from './Menu/Menu';
import {Settings} from './Settings/Settings';

interface IPropsTypes {
    burgerCondition: boolean
    setBurgerCondition: (condition: boolean) => void
}

export const Toolbar: React.FC<IPropsTypes> = ({burgerCondition, setBurgerCondition}) => {
    return (
        <div className={burgerCondition ? `${styles.toolbar} ${styles.toolbarActive}` : styles.toolbar}>
            <Menu setBurgerCondition={setBurgerCondition}/>
            <Settings/>
        </div>
    )
};

