//other import
import React, {useState} from 'react';
//styles
import styles from './Header.module.scss';
//my components
import {Logo} from './Logo/Logo';
import {Toolbar} from './Toolbar/Toolbar';

export const Header = () => {

    const [burgerCondition, setBurgerCondition] = useState(false)

    return (
        <div className={styles.headerWrapper}>
            <header className={`${styles.header} container`}>
                <Logo/>
                <Toolbar burgerCondition={burgerCondition} setBurgerCondition={setBurgerCondition}/>
                <div onClick={() => setBurgerCondition(false)} className={burgerCondition ? `${styles.eclipse} ${styles.eclipseActive}` : styles.eclipse}>ECLIPSE</div>
                <div onClick={() => setBurgerCondition(!burgerCondition)} className={burgerCondition ? `${styles.burgerBtn} ${styles.burgerBtnActive}` : styles.burgerBtn}>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </header>
        </div>
    )
};

