//other import
import React from 'react';
import {NavLink} from 'react-router-dom';
import { Animated } from 'react-animated-css';
//styles
import styles from './Basket.module.scss';
//my components
import {ProductCard} from '../ProductsField/ProductCard/ProductCard';

interface IPropsTypes {
    basketData: Array<IProductObject>
    toggleToBasket: (product: IProductObject) => void
    plus: (id: string) => void
    minus: (id: string) => void
    setSinglePurchaseData: (product: IProductObject) => void
}

export const Basket: React.FC<IPropsTypes> = ({basketData, toggleToBasket, plus, minus, setSinglePurchaseData}) => {

    const basketDataAfterMap = basketData.map((product) => <ProductCard cardStyle={2}
                                                                        key={product.id}
                                                                        productData={product}
                                                                        minus={minus}
                                                                        plus={plus}
                                                                        basketData={basketData}
                                                                        setSinglePurchaseData={setSinglePurchaseData}
                                                                        toggleToBasket={toggleToBasket}/>)

    return (
        <div className={`${styles.basket} container`}>
            {basketDataAfterMap}
            {basketDataAfterMap.length > 0
                ? <NavLink to='/check/basket' className={styles.button}>Підтвердити замовлення</NavLink>
                : <Animated animationIn="zoomInDown" animationInDuration={1000} animationOut="fadeOut" isVisible={true} className={styles.empty}>Корзина порожня :((</Animated>}
        </div>
    )
};